/*colors*/

$almost-white: hsl(0, 0%, 98%);
$medium-gray: hsl(0, 0%, 41%);
$almost-black: hsl(0, 0%, 8%);
$overlay:  hsl(0, 0%, 41%, .8);

/*font-weights*/
$medium: 500;
$bold: 700;

/*font-size*/
$para: 16px;
$nav: 14px;
$head: 2rem;

/*border-radius*/
$border-radius: 15px;

/* breakpoints*/
$breakpoints: (
  'xs': 0,
  'sm': 480px,
  'md': 720px,
  'lg': 960px,
  'xl': 1200px
);

/*mixins*/
@mixin xs {
  @media (min-width: map-get($breakpoints, 'xs')) {
    @content;
  }
}
@mixin sm {
  @media (min-width: map-get($breakpoints, 'sm')) {
    @content;
  }
}
@mixin md {
  @media (min-width: map-get($breakpoints, 'md')) {
    @content;
  }
}
@mixin lg {
  @media (min-width: map-get($breakpoints, 'lg')) {
    @content;
  }
}
@mixin xl {
  @media (min-width: map-get($breakpoints, 'xl')) {
    @content;
  }
}